import React, { useState, useEffect } from 'react';
import { client } from '../sanity/lib/client';

const JobApplicationForm = ({ jobId }) => {
    const [job, setJob] = useState(null);
    const [form, setForm] = useState(null);
    const [formData, setFormData] = useState({});
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [submitMessage, setSubmitMessage] = useState('');

    // Fetch job data and associated form
    useEffect(() => {
        const fetchData = async () => {
            try {
                // Fetch the job document with the form reference
                const jobData = await client.fetch(
                    `*[_type == "job" && _id == $jobId][0]{
            ...,
            chooseForm->
          }`,
                    { jobId }
                );

                setJob(jobData);
                setForm(jobData.chooseForm);

                // Initialize form data object
                if (jobData.chooseForm?.fields) {
                    const initialData = {};
                    jobData.chooseForm.fields.forEach(field => {
                        initialData[field.name] = '';
                    });
                    setFormData(initialData);
                }
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, [jobId]);

    const handleChange = (e) => {
        const { name, value, type, checked } = e.target;
        setFormData({
            ...formData,
            [name]: type === 'checkbox' ? checked : value,
        });
    };

    const handleFileChange = (e) => {
        const { name, files } = e.target;
        setFormData({
            ...formData,
            [name]: files[0],
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsSubmitting(true);

        try {
            // Here you would typically send the form data to your backend
            console.log('Form submitted:', formData);

            // Simulate API call
            await new Promise(resolve => setTimeout(resolve, 1000));

            setSubmitMessage('Application submitted successfully!');
            // Reset form after successful submission
            const resetData = {};
            form.fields.forEach(field => {
                resetData[field.name] = '';
            });
            setFormData(resetData);
        } catch (error) {
            console.error('Submission error:', error);
            setSubmitMessage('Error submitting application. Please try again.');
        } finally {
            setIsSubmitting(false);
        }
    };

    if (!form) {
        return <div>Loading form...</div>;
    }

    return (
        <div className="max-w-2xl mx-auto p-6 bg-white rounded-lg shadow-md">
            <h2 className="text-2xl font-bold mb-6">{form.title || 'Job Application'}</h2>

            <form onSubmit={handleSubmit} className="space-y-4">
                {form.fields.map((field, index) => (
                    <div key={index} className="space-y-2">
                        {!field.showPlaceholder && (
                            <label htmlFor={field.name} className="block text-sm font-medium text-gray-700">
                                {field.label}
                                {field.isRequired && <span className="text-red-500">*</span>}
                            </label>
                        )}

                        {field.type === 'textarea' ? (
                            <textarea
                                id={field.name}
                                name={field.name}
                                value={formData[field.name] || ''}
                                onChange={handleChange}
                                placeholder={field.showPlaceholder ? field.label : field.placeholder}
                                required={field.isRequired}
                                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                                rows={4}
                            />
                        ) : field.type === 'checkbox' ? (
                            <div className="flex items-center">
                                <input
                                    id={field.name}
                                    name={field.name}
                                    type="checkbox"
                                    checked={formData[field.name] || false}
                                    onChange={handleChange}
                                    className="h-4 w-4 rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                                />
                                <label htmlFor={field.name} className="ml-2 block text-sm text-gray-700">
                                    {field.label}
                                </label>
                            </div>
                        ) : field.type === 'file' ? (
                            <input
                                id={field.name}
                                name={field.name}
                                type="file"
                                onChange={handleFileChange}
                                required={field.isRequired}
                                className="block w-full text-sm text-gray-500
                  file:mr-4 file:py-2 file:px-4
                  file:rounded-md file:border-0
                  file:text-sm file:font-semibold
                  file:bg-blue-50 file:text-blue-700
                  hover:file:bg-blue-100"
                            />
                        ) : (
                            <input
                                id={field.name}
                                name={field.name}
                                type={field.type}
                                value={formData[field.name] || ''}
                                onChange={handleChange}
                                placeholder={field.showPlaceholder ? field.label : field.placeholder}
                                required={field.isRequired}
                                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                            />
                        )}
                    </div>
                ))}

                <div className="pt-4">
                    <button
                        type="submit"
                        disabled={isSubmitting}
                        className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                        {isSubmitting ? 'Submitting...' : form.submitButtonText || 'Submit'}
                    </button>
                </div>

                {submitMessage && (
                    <p className={`mt-3 text-sm ${submitMessage.includes('Error') ? 'text-red-500' : 'text-green-500'}`}>
                        {submitMessage}
                    </p>
                )}
            </form>
        </div>
    );
};

export default JobApplicationForm;